import styled from "styled-components"
import { MdKeyboardArrowLeft, MdKeyboardArrowRight } from "react-icons/md";

export const PortfolioContainer = styled.div`
    height: 700px;
    background-color: #1A1525;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (min-width: 768px) {
        height: 780px;
    }
`

export const Heading = styled.h1`
    color: #fff;
    margin-top: 100px;
    font-size: 28px;
    font-weight: 600;
    line-height: 36px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 32px;
        line-height: 38px;
        margin-top: 80px;
    }
`

export const SubHeading = styled.h2`
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 50px;
    letter-spacing: 1.5px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 15px;
    }
`

export const PortfolioCard = styled.div`
    height: 500px;
    width: 75%;
    display: flex;
    flex-direction: column;
    transition: all 0.3s ease-in-out;

    @media screen and (min-width: 768px) {
        width: 85%;
    }
`


export const Image = styled.img`
    display: block;
    border-radius: 5px;
    max-width: 100%;
    max-height: 100%;
    align-self: center;
    margin-bottom: 15px;
    transition: all 0.2s ease-in-out;
    cursor: pointer;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.02);
    }
 `

export const CardHeading = styled.h2`
    margin-top: 20px;
    margin-bottom: 15px;
    color: #fff;
    align-self: flex-start;
    font-size: 20px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 21px;
    }
`
export const Description = styled.p`
    align-self: flex-start;
    font-weight: 400;
    font-size: 14px;
    color: #767676;
    margin-bottom: 25px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.01);
    }

    @media screen and (min-width: 768px) {
        font-size: 16px;
    }
`

export const Link = styled.a`
    display: flex;
    text-decoration: none;
    color: #fff;
    font-size: 18px;
    background-color: #8777D9;
    width: 85px;
    height: 45px;
    border-radius: 8px;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        background-color: #6C55E0;
        font-size: 20px;
    }
`

export const LeftArrow = styled(MdKeyboardArrowLeft)`
    position: absolute;
    top: 2600px;
    left: 0;
    font-size: 2.8em;
    color: #6C55E0;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.5);
    }

    @media screen and (min-width: 768px) {
        top: 2760px;
    }

`

export const RightArrow = styled(MdKeyboardArrowRight)`
    position: absolute;
    top: 2600px;
    right: 0;
    font-size: 2.8em;
    color: #6C55E0;
    transition: all 0.2s ease-in-out;
    cursor: pointer;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.5);
    }

    @media screen and (min-width: 768px) {
        top: 2760px;
    }
`