import styled from "styled-components";
import {Link} from "react-scroll";
import {FaBars} from "react-icons/fa";

export const ScrollBarContainer = styled.div`
    width: 100%;
    height: 100%;
    position: fixed;
    top: ${({NavState}) => (NavState ? "-100%":"0")};
    bottom: 0;
    right: 0;
    left: 0;
    padding: 85px;
    background-color: #fff;
    align-items: center;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    transition: all 0.5s ease-in-out;
    z-index: 999;

    &:hover {
        transition: all 0.5s ease-in-out;
    }
`

export const NavLinks = styled(Link)`
    color: #000;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease-in-out;

    &:hover {
        color: #8777D9;
        transition: all 0.2s ease-in-out;
        transform: scale(1.1);
    }
`

export const NavBtn = styled.button`
    z-index: 1000;
`

export const NavButton = styled(FaBars)`
    display: none;
    src: FaBars;
    color: #000;
    cursor: pointer;
    width: 24px;
    height: 24px;
    z-index:1000;

    transition: all 0.2s ease-in-out;

    &:hover {
        color: #8777D9;
        transition: all 0.2s ease-in-out;
        transform: scale(1.2);
    }

    @media screen and (max-width: 1100px) {
        display: flex;
        position: absolute;
        top: 36px;
        right: 20px;
    }
`