import React, {useState} from 'react'
import { ScrollBarContainer, NavLinks, NavButton, NavBtn } from './ScrollBarElements'


const ScrollBar = () => {
    const [NavState, NavStateSetter] = useState("false");

    const toggleState = () => {
        NavStateSetter(!NavState);
        console.log(NavState);
    }

    return (
        <>
            <NavBtn onClick={toggleState}>
                <NavButton />
            </NavBtn>
            <ScrollBarContainer NavState={NavState} >
                <NavLinks to="home" activeClass="active" duration={500} smooth={true} spy={true} onClick={toggleState}>Home</NavLinks>
                <NavLinks to="about" activeClass="active" duration={500} smooth={true} spy={true} onClick={toggleState}>About</NavLinks>
                <NavLinks to="career" activeClass="active" duration={650} smooth={true} spy={true} onClick={toggleState}>Career</NavLinks>
                <NavLinks to="portfolio" activeClass="active" duration={800} smooth={true} spy={true}onClick={toggleState}>Portfolio</NavLinks>
                <NavLinks to="contactme" activeClass="active" duration={1000} smooth={true} spy={true} onClick={toggleState}>Contact Me</NavLinks>
            </ScrollBarContainer>
        </>
    )
}

export default ScrollBar
