import React from 'react'
import { NavBarContainer, NavLogo, NavLinksWrapper, NavLinks } from './NavBarElements'

const NavBar = () => {

    return (
        <NavBarContainer id="home">
            <NavLogo>Askari</NavLogo>
            <NavLinksWrapper>
                <NavLinks to="home" activeClass="active" duration={500} smooth={true} spy={true}>Home</NavLinks>
                <NavLinks to="about" activeClass="active" duration={500} smooth={true} spy={true}>About</NavLinks>
                <NavLinks to="career" activeClass="active" duration={650} smooth={true} spy={true}>Career</NavLinks>
                <NavLinks to="portfolio" activeClass="active" duration={800} smooth={true} spy={true}>Portfolio</NavLinks>
                <NavLinks to="contactme" activeClass="active" duration={1000} smooth={true} spy={true}>Contact Me</NavLinks>
            </NavLinksWrapper>
        </NavBarContainer>
    )
}

export default NavBar
