import React, {useState} from 'react'
import { CareerContainer, CareerWrapper, Heading, SubHeading, BtnWrapper, Education, LogoEdu, LogoWork, Work, Text, Img1, Img2 } from "./CareerElements"
import edu from "../../images/graduation-cap.svg";
import work from "../../images/briefcase-alt.svg";
import eduPic from "../../images/Education2.svg";
import workPic from "../../images/Education3.svg";

const CareerSection = () => {
    const [isEducation,setIsEducation] = useState(true);

    const activate = () => {
        setIsEducation(true);
    }

    const deActivate = () => {
        setIsEducation(false);
    }

    return (
        <>
            <CareerContainer id="career">
                <CareerWrapper>
                    <Heading>Qualifications</Heading>
                    <SubHeading>My personal journey</SubHeading>
                    <BtnWrapper>
                        <Education onClick={activate} isActive={isEducation}>
                            <LogoEdu src={edu}></LogoEdu>
                            <Text isActive={isEducation}>Education</Text>
                        </Education>
                        <Work onClick={deActivate} isActive={!isEducation}>
                            <LogoWork src={work}></LogoWork>
                            <Text isActive={!isEducation}>Work</Text>
                        </Work>
                    </BtnWrapper>
                    <Img1 src={eduPic} isActive={isEducation} />
                    <Img2 src={workPic} isActive={isEducation} />
                </CareerWrapper>
            </CareerContainer>
        </>
    )
}

export default CareerSection;
