import styled from "styled-components";

export const ContactMeContainer = styled.div`
    height: 1075px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media screen and (min-width: 768px) {
        height: 1075px;
    }
`

export const ContactWrapper = styled.div`
    height: 95%;
    width: 90%;
    display: flex;
    flex-direction: column;
    @media screen and (min-width: 768px) {
        justify-content: center;
        align-items: center;
    }
`



export const Heading = styled.h1`
    color: #000;
    margin-top: 70px;
    font-size: 30px;
    font-weight: 600;
    line-height: 36px;
    transition: all 0.2s ease-in-out;
    align-self: center;
    justify-self: center;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 32px;
        line-height: 38px;
        margin-top: 50px;
    }
`

export const SubHeading = styled.h2`
    color: #000;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 50px;
    letter-spacing: 1.5px;
    align-self: center;
    justify-self: center;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 15px;
        margin-bottom: 80px;
    }
`

export const PersonalCollection = styled.div`
    @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 750px;
        align-self: center;
        justify-self: center;
    }
`

export const PersonalWrapper = styled.div`
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-self: center;
    align-self: flex-start;
    justify-content: flex-start;
    margin-bottom: 20px;
    @media screen and (min-width: 768px) {
        align-self: center;
        justify-content: center;
    }
`

export const Column1 = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`

export const Column2 = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
`

export const Img = styled.img`
    width: 30px;
    height: 30px;
    margin-right: 15px;
    transition: all 0.2s ease-in-out;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.08);
    }
`

export const MiniHeading = styled.h2`
    font-size: 18px;
    font-weight: 500;
    transition: all 0.2s ease-in-out;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.03);
    }
`

export const MiniDes = styled.p`
    font-size: 14px;
    color: #939093;
    transition: all 0.2s ease-in-out;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.03);
    }
`

export const FormCollection = styled.div`
    @media screen and (min-width: 768px) {
        display: flex;
        width: 500px;
        align-items: center;
        justify-content: center;
    }
`

export const Form = styled.form`
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    width: 100%;
    @media screen and (min-width: 768px) {
        align-items: center;
        justify-content: center;
    }
`

export const InputName = styled.input`
    width: 100%;
    height: 70px;
    margin-top: 1px;
    padding: 10px;
    background-color: #D5D6E2;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 20px;

    @media screen and (min-width: 768px) {
        align-self: center;
        justify-content: center;
    }
`

export const InputEmail = styled.input`
    width: 100%;
    height: 70px;
    margin-top: 1px;
    padding: 10px;
    background-color: #D5D6E2;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 20px;
`

export const InputProject = styled.input`
    width: 100%;
    height: 70px;
    margin-top: 1px;
    padding: 10px;
    background-color: #D5D6E2;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 20px;
`

export const Message = styled.textarea`
    box-sizing: border-box;
    width: 100%;
    height: 200px;
    margin-top: 1px;
    padding: 10px;
    background-color: #D5D6E2;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 20px;
    resize: none;
`

export const Submit = styled.button`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 400;
    letter-spacing: -0.03em;
    line-height: 36px;
    border: none;
    margin: 10px 0;
    width: 210px;
    height: 61px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    background-color: #8777D9;
    
    &:hover {
        transition: all 0.2s ease-in-out;
        background-color: #6C55E0;
        transform: scale(1.02);
    }

    @media screen and (max-width: 768px) {
        font-size: 20px;
        width: 220px;
        height: 55px;
    }
`

export const Send = styled.img`
    width: 25px;
    height: 25px;
    margin-left: 15px;
    font-size: 24px;
`