import React from 'react'
import { FooterContainer, FooterWrapper, Heading, SubHeading, NavLinks, HeadingWrapper, Text1, LinksWrapper, PersonalLinks, Text, Link, Logo, FiverrLogo } from "./FooterElements";
import gitSrc from "../../images/github-alt copy.svg"
import linkedInSrc from "../../images/linkedin-alt copy.svg"

const Footer = () => {
    return (
        <>
            <FooterContainer id="footer">
                <FooterWrapper>
                    <HeadingWrapper>
                        <Heading to="home" activeClass="active" duration={1400} smooth={true} spy={true}>Askari.</Heading>
                        <SubHeading>FrontEnd Developer</SubHeading>
                    </HeadingWrapper>
                    <LinksWrapper>
                        <NavLinks to="career" activeClass="active" duration={1000} smooth={true} spy={true}>Career</NavLinks>
                        <NavLinks to="portfolio" activeClass="active" duration={650} smooth={true} spy={true}>Portfolio</NavLinks>
                        <NavLinks to="contactme" activeClass="active" duration={500} smooth={true} spy={true}>Contact Me</NavLinks>
                    </LinksWrapper>
                    <PersonalLinks>
                        <Link href="https://github.com/askarihriz" target="_blank">
                            <Logo src={gitSrc} alt="github link" />
                        </Link>
                        <Link href="https://www.linkedin.com/in/askari-hassan/" target="_blank">
                            <Logo src={linkedInSrc} alt="LinkedIn link" />
                        </Link>
                        <Link href="https://www.fiverr.com/askarihriz" target="_blank">
                            <FiverrLogo />
                        </Link>
                    </PersonalLinks>
                    <Text1>© Askari. All rights reserved.</Text1>
                </FooterWrapper>
                <Text>© Askari. All rights reserved.</Text>
            </FooterContainer>
        </>
    )
}

export default Footer
