import React from 'react';
import { ContactMeContainer, ContactWrapper, FormCollection, PersonalCollection, Submit, Message, Send, InputEmail, InputProject, Form, InputName, Img, MiniHeading, MiniDes , Heading, SubHeading, PersonalWrapper, Column1, Column2 } from './ContactMeElements';
import Call from "../../images/phone.svg";
import Email from "../../images/envelope.svg";
import DropOff from "../../images/map-marker.svg";
import sen from "../../images/message.svg"
import emailjs from "emailjs-com";

const ContactMeSection = () => {
    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm("service_5a594w9", "template_iyrjk08", e.target, "user_cBnePZoA2ClIkg494rYSn")
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });

        e.target.reset();
    }

    return (
        <>
            <ContactMeContainer id="contactme">
                <ContactWrapper>
                    <Heading>Contact Me</Heading>
                    <SubHeading>Get in touch</SubHeading>
                    <PersonalCollection>
                        <PersonalWrapper>
                            <Column1>
                                <Img src={Call} />
                            </Column1>
                            <Column2>
                                <MiniHeading>Call Me</MiniHeading>
                                <MiniDes>+92 3312253287</MiniDes>
                            </Column2>
                        </PersonalWrapper>
                        <PersonalWrapper>
                            <Column1>
                                <Img src={Email} />
                            </Column1>
                            <Column2>
                                <MiniHeading>Email</MiniHeading>
                                <MiniDes>askarihriz@gmail.com</MiniDes>
                            </Column2>
                        </PersonalWrapper>
                        <PersonalWrapper>
                            <Column1>
                                <Img src={DropOff} />
                            </Column1>
                            <Column2>
                                <MiniHeading>Location</MiniHeading>
                                <MiniDes>Karachi, Pakistan</MiniDes>
                            </Column2>
                        </PersonalWrapper>
                    </PersonalCollection>
                    <FormCollection onSubmit={sendEmail}>
                        <Form>
                            <InputName type="text" placeholder="Name" name="name"/>
                            <InputEmail type="email" placeholder="Email" name="email"/>
                            <InputProject type="text" placeholder="Project" name="subject"/>
                            <Message type="text" placeholder="Message" name="message"/>
                            <Submit type="submit" >Send Message <Send src={sen}></Send></Submit>
                        </Form>
                    </FormCollection>
                </ContactWrapper>
            </ContactMeContainer>
        </>
    )
}

export default ContactMeSection
