import styled from "styled-components";

export const CareerContainer = styled.div`
    height: 700px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media screen and (min-width: 768px) {
        height: 780px;
    }
`

export const CareerWrapper = styled.div`
    height: 90%;
    width: 85%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
`

export const Heading = styled.h1`
    color: #000;
    margin-top: 70px;
    font-size: 30px;
    font-weight: 600;
    line-height: 36px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 32px;
        line-height: 38px;
        margin-top: 50px;
    }
`

export const SubHeading = styled.h2`
    color: #000;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 60px;
    letter-spacing: 1.5px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 15px;
    }
`

export const BtnWrapper = styled.div`
    display: flex;
    width: 90%;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 60px;

    @media screen and (min-width: 768px) {
        width: 80%;
        margin-bottom: 50px;
    }
`

export const Education = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding-bottom: 2px;
    transition: all 0.2s ease-in-out;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }
    border-bottom: 3px solid ${({isActive}) => ( isActive ? "#8777D9": "#fff" )};

    @media screen and (min-width: 768px) {
        border-bottom: 4px solid ${({isActive}) => ( isActive ? "#8777D9": "#fff" )};
    }
`

export const LogoEdu = styled.img`
    width: 28px;
    height: 28px;
    margin-right: 8px;
`

export const Text = styled.p`
    font-size: 20px;
    font-weight: 500;
    color: ${({isActive}) => ( isActive ? "#8777D9": "#767676" )};
    transition: all 0.2s ease-in-out;
`

export const Work = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding-bottom: 2px;
    transition: all 0.2s ease-in-out;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }
    border-bottom: 3px solid ${({isActive}) => ( isActive ? "#8777D9": "#fff" )};

    @media screen and (min-width: 768px) {
        border-bottom: 4px solid ${({isActive}) => ( isActive ? "#8777D9": "#fff" )};
    }
`

export const LogoWork = styled.img`
    width: 25px;
    height: 25px;
    margin-right: 8px;

`

export const Img1 = styled.img`
    width: 90%;
    display: ${({isActive}) => ( isActive ? "block": "none" )};
`

export const Img2 = styled.img`
    width: 90%;
    margin-top: 50px;
    display: ${({isActive}) => ( isActive ? "none": "block" )};
    @media screen and (min-width: 768px) {
        margin-top: 80px;
    }
`