import React from 'react'
import Typical from 'react-typical';
import { HeroContainer, Column1, Column2, HeroWrapper, FiverrLogo, ScrollDownSection2, Mouse, Logo, PersonalLinks, Link, ScrollText, Arrow, Img, Heading, SubHeading, ScrollDownSection, Para, ContactButton, Send } from './HeroElements'
import source from "../../images/Pic.svg"
import sen from "../../images/message.svg"
import mouseSrc from "../../images/mouse-alt.svg"
import Arr from "../../images/arrow-down.svg"
import gitSrc from "../../images/github-alt.svg"
import linkedInSrc from "../../images/linkedin-alt.svg"


const HeroSection = () => {
    return (
        <HeroContainer>
            <PersonalLinks>
                <Link href="https://github.com/askarihriz" target="_blank">
                    <Logo src={gitSrc} alt="github link" />
                </Link>
                <Link href="https://www.linkedin.com/in/askari-hassan/" target="_blank">
                    <Logo src={linkedInSrc} alt="LinkedIn link" />
                </Link>
                <Link href="https://www.fiverr.com/askarihriz" target="_blank">
                    <FiverrLogo />
                </Link>
            </PersonalLinks>
            <HeroWrapper>
                <ScrollDownSection2>
                    <Mouse src={mouseSrc} alt="Mouse Scroll"/>
                    <ScrollText>scroll down</ScrollText>
                    <Arrow src={Arr} alt="arrow"/>
                </ ScrollDownSection2>
                <Column1>
                    <Heading>Hi, I'm Askari</Heading>
                    <SubHeading><Typical steps={["Ui / Ux Designer", 2000, "FrontEnd Developer", 2000, "Software Engineer", 2000]} loop={Infinity} wrapper="b"></Typical></SubHeading>
                    <Para>High level experience in web design and development knowledge, producing quality work.</Para>
                    <ContactButton to="contactme" activeClass="active" duration={1000} smooth={true} spy={true}>Contact Me{<Send src={sen} alt="send" />}</ContactButton>
                </Column1>
                <Column2>
                    <Img src={source} alt="Image" />
                </Column2>
                <ScrollDownSection>
                    <Mouse src={mouseSrc} alt="Mouse Scroll"/>
                    <ScrollText to="about" activeClass="active" duration={500} smooth={true} spy={true}>scroll down</ScrollText>
                    <Arrow src={Arr} alt="arrow"/>
                </ ScrollDownSection>
            </HeroWrapper>
        </HeroContainer>
    )
}

export default HeroSection
