import styled from "styled-components"
import { Link as LinkS } from "react-scroll"
import { SiFiverr } from "react-icons/si"

export const HeroContainer = styled.div`
    height: 650px ;
    background-color: #fff;
    display: flex;
    justify-content: center;

    @media screen and (max-width: 900px) {
        height: 1000px;
    }

    @media screen and (max-width: 768px) {
        height: 800px;
    }
`

export const PersonalLinks = styled.div`
    display: flex;
    flex-direction: column;
    margin-top: 175px;
    padding-right: 175px;

    @media screen and (max-width: 1100px) {
        display: none;
    }
`

export const Link = styled.a`
    margin-bottom: 28px;
`

export const Logo = styled.img`
    height: 30px;
    width: 30px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.1);
    }
`

export const FiverrLogo = styled(SiFiverr)`
    src: SiFiverr;
    height: 30px;
    width: 30px;
    fill: #8777D9;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.1);
    }
`

export const HeroWrapper = styled.div`
    display: grid;
    margin-top: 50px;
    grid-template-columns: 1.7fr 1fr;
    align-items: center;
    justify-content: center;
    //background-color: blue;
    grid-gap: 20px;
    height: 400px;
    width: 60%;

    @media screen and (max-width: 900px) {
        grid-template-columns: 1fr;
        display: flex;
        flex-direction: column-reverse;
        height: 900px;
    }
    
    @media screen and (max-width: 768px) {
        width: 80%;
        height: 700px;
    }
`

export const Column1 = styled.div`
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding: 32px 0;

    @media screen and (max-width: 768px) {
        align-items: center;
        justify-content: center;
        padding: 0px;
    }
`

export const Heading = styled.h1`
    font-size: 64px;
    font-weight: 600;
    line-height: 96px;
    white-space: nowrap;

    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.01);
    }

    @media screen and (max-width: 768px) {
        font-size: 48px;
    }
    @media screen and (max-width: 480px) {
        font-size: 40px;
    }
`

export const SubHeading = styled.h2`
    font-size: 27px;
    font-weight: 600;
    line-height: 40px;
    letter-spacing: 0.04em;
    color: #767676;
    margin-bottom: 5px;
    margin-left: 8px;

    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.01);
    }

    @media screen and (max-width: 768px) {
        font-size: 25px;
        margin-left: 0px;
    }

    @media screen and (max-width: 480px) {
        font-size: 22px;
    }
`

export const Para = styled.p`
    font-size: 24px;
    line-height: 33px;
    font-weight: 400;
    color: #959595;
    margin-top: 11px;
    margin-left: 8px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.01);
    }

    @media screen and (max-width: 768px) {
        font-size: 20px;
        margin-left: 0px;
        text-align: center;
    }

    @media screen and (max-width: 480px) {
        line-height: 29px;
        font-size: 17px;
    }
`

export const Column2 = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    padding-right: 20px;

    @media screen and (max-width: 768px) {
        padding-right: 0;
    }
    
`

export const Img = styled.img`
    width: 290px;
    height: 330px;

    @media screen and (max-width: 480px) {
        padding: 20px;
    }
`

export const ContactButton = styled(LinkS)`
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    border-radius: 15px;
    font-size: 24px;
    font-weight: 400;
    letter-spacing: -0.03em;
    line-height: 36px;
    border: none;
    margin: 44px 0;
    width: 210px;
    height: 61px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    background-color: #8777D9;
    
    &:hover {
        transition: all 0.2s ease-in-out;
        background-color: #6C55E0;
        transform: scale(1.02);
    }

    @media screen and (max-width: 768px) {
        font-size: 20px;
        width: 200px;
        height: 55px;
    }
`

export const Send = styled.img`
    width: 25px;
    height: 25px;
    margin-left: 15px;
    font-size: 24px;
`

export const ScrollDownSection = styled.div`
    display: flex;
    height: 40px;
    align-items: center;

    @media screen and (max-width: 900px) {
        display: none;
    }
`

export const ScrollDownSection2 = styled.div`
    height: 40px;
    align-items: center;
    display: none;

    @media screen and (max-width: 900px) {
        display: flex;
        align-self: flex-start;
    }

    @media screen and (max-width: 768px) {
        display: none;
    }
`

export const Mouse = styled.img`
    height: 43px;
    width: 39px;
`

export const ScrollText = styled(LinkS)`
    color: #767676;
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    letter-spacing: -0.03em;
    margin-left: 5px;
    cursor:pointer;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

`

export const Arrow = styled.img`
    margin-left: 5px;
    height: 30px;
    width: 30px;
`