import styled from "styled-components";

export const AboutContainer = styled.div`
    height: 700px;
    background-color: #1A1525;
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (min-width: 768px) {
        height: 780px;
    }
`

export const AboutWrapper = styled.div`
    height: 90%;
    width: 85%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
`

export const Heading = styled.h1`
    color: #fff;
    margin-top: 40px;
    font-size: 28px;
    font-weight: 600;
    line-height: 36px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 32px;
        line-height: 38px;
    }
`

export const SubHeading = styled.h2`
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 30px;
    letter-spacing: 1.5px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 15px;
    }
`

export const Description = styled.p`
    align-self: flex-start;
    text-align: center;
    font-weight: 400;
    font-size: 14px;
    color: #767676;
    margin-bottom: 25px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.01);
    }

    @media screen and (min-width: 768px) {
        font-size: 16px;
    }
`

export const ExperienceWrapper = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    margin-bottom: 35px;
`

export const ColumnWrapper = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
`

export const ExpHeading = styled.h2`
    color: #fff;
    font-weight: 600;
    font-size: 22px;
    letter-spacing: 1px;
    transition: all 0.2s ease-in-out;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }
`

export const Text = styled.p`
    color: #767676;
    font-size: 12px;
    text-align: center;
    transition: all 0.2s ease-in-out;
    font-weight: 400;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.03);
    }
`

export const PlaceHolder = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
`

export const Image = styled.img`
    display: block;
    border-radius: 3px;
    width: 40%;
    margin: auto;
    margin-bottom: 30px;
    transition: all 0.2s ease-in-out;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.01);
    }
`

export const DownloadButton = styled.a`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 65%;
    height: 40px;
    background-color: #8777D9;
    border-radius: 10px;
    text-decoration: none;
    transition: all 0.2s ease-in-out;
    &:hover {
        background-color: #6C55E0;
        transition: all 0.2s ease-in-out;
        transform: scale(1.03);
    }

    @media screen and (min-width: 768px) {
        width: 45%;
    }
`

export const BtnText = styled.p`
    color: #fff;
    font-size: 18px;
    font-weight: 400;
`

export const Icon = styled.img`
    width: 22px;
    height: 22px;
    margin-left: 8px;
`