import React from 'react';
import SwiperCore, { Navigation, Pagination, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.css';
import "./styles.css";
import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import { PortfolioContainer, Heading, PortfolioCard, Image, SubHeading, Description, CardHeading, LeftArrow, RightArrow, Link } from './PortfolioElements';
import image1 from "../../images/Virtual Banking app 2.png";
import image2 from "../../images/HiCal2.png";
import image3 from "../../images/PIN Strength meter.png";
import image4 from "../../images/dartCopy.png";
import image5 from "../../images/duckShooter - Copy.png";
import image6 from "../../images/AI Flappy Bird.png";

const  PortfolioSection = () => {

    SwiperCore.use([Navigation, Pagination, Autoplay]);

    const headingData = ["Modern Website", "Calculator App", "PIN Strength Meter", "Carnival Dart Game", "Duck Shootout", "AI Flappy Bird" ];
    const imageData = [ image1, image2, image3, image4, image5, image6 ];
    const descriptionData = ["React Website with smooth scroll, adaptable to all devices with ui components and animated interactions.", "Simple React calculator app, fully responsive and adaptable to different devices. It was an experimental project with Material Ui.", "Security application created in order to determine vulnerability of a PIN. Full fletched and responsive react application with Material Ui support.", "Fun arcade game, recreation of real life carnival dart game. Simple, straight forward, 2D made using SDL2 library and C++.", "First person duck shooting game inspired by old retro graphics from PlayStation 1, created using my own 2D GameEngine.", "Good old Flappy Bird recreated using SDL2 Framework and C++, revised with an AI such that the game play by itself infinitly." ];

    return (
        <>
            <PortfolioContainer id="portfolio">
                <Heading>Portfolio</Heading>
                <SubHeading>Most recent work</SubHeading>
                <LeftArrow />
                <RightArrow />
                <PortfolioCard>
                    <Swiper className="notSwiper" breakpoints={{ 480: { slidesPerView: 1 }, 768: { slidesPerView: 2, spaceBetween: 40 }, 1100: { slidesPerView: 3, spaceBetween: 30 }}}  spaceBetween={100} pagination={{clickable: true}} loop autoplay={{ delay: 3000, disableOnInteraction: false }} >
                        <SwiperSlide className="notSlide">
                            <Image src={imageData[0]}></Image>
                            <CardHeading>{headingData[0]}</CardHeading>
                            <Description>{descriptionData[0]}</Description>
                            <Link href="https://askarihriz.github.io/VirtualBankingApp/" target="_blank">Demo</Link>
                        </SwiperSlide>
                        <SwiperSlide className="notSlide">
                            <Image src={imageData[5]}></Image>
                            <CardHeading>{headingData[5]}</CardHeading>
                            <Description>{descriptionData[5]}</Description>
                            <Link href="https://github.com/askarihriz/NNFlappyBird" target="_blank">Build</Link>
                        </SwiperSlide>
                        <SwiperSlide className="notSlide">
                            <Image src={imageData[1]}></Image>
                            <CardHeading>{headingData[1]}</CardHeading>
                            <Description>{descriptionData[1]}</Description>
                            <Link href="https://askarihriz.github.io/Calculator/" target="_blank">Demo</Link>
                        </SwiperSlide>
                        <SwiperSlide className="notSlide">
                            <Image src={imageData[4]}></Image>
                            <CardHeading>{headingData[4]}</CardHeading>
                            <Description>{descriptionData[4]}</Description>
                            <Link href="https://github.com/askarihriz/DuckShooter" target="_blank">Build</Link>
                        </SwiperSlide>
                        <SwiperSlide className="notSlide">
                            <Image src={imageData[2]}></Image>
                            <CardHeading>{headingData[2]}</CardHeading>
                            <Description>{descriptionData[2]}</Description>
                            <Link href="https://askarihriz.github.io/PinAuthenticator/" target="_blank">Demo</Link>
                        </SwiperSlide>
                        <SwiperSlide className="notSlide">
                            <Image src={imageData[3]}></Image>
                            <CardHeading>{headingData[3]}</CardHeading>
                            <Description>{descriptionData[3]}</Description>
                            <Link href="https://github.com/askarihriz/Carnival-Dart-Game" target="_blank">Build</Link>
                        </SwiperSlide>
                    </Swiper>
                </PortfolioCard>
            </PortfolioContainer>
        </>
    )
}

export default PortfolioSection;
