import styled from "styled-components";
import {Link as LinkS} from "react-scroll";
import { SiFiverr } from "react-icons/si"

export const FooterContainer = styled.div`
    height: 550px;
    background-color: #8777D9;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media screen and (min-width: 768px) {
        height: 300px;
    }
`

export const FooterWrapper = styled.div`
    width: 90%;
    height: 90%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    @media screen and (min-width: 768px) {
        align-items: center;
        justify-content: space-evenly;
        flex-direction: row;
        height: 80%;
    }
`

export const Heading = styled(LinkS)`
    color: #fff;
    margin-top: 70px;
    font-size: 30px;
    font-weight: 600;
    line-height: 36px;
    transition: all 0.2s ease-in-out;
    cursor: pointer;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 32px;
        line-height: 38px;
        margin-top: 50px;
    }
`

export const SubHeading = styled.h2`
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 50px;
    letter-spacing: 1.5px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.05);
    }

    @media screen and (min-width: 768px) {
        font-size: 15px;
        margin-bottom: 40px;
    }
`

export const NavLinks = styled(LinkS)`
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    margin-bottom: 10px;

    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.1);
    }

    @media screen and (min-width: 768px) {
        margin-right: 30px;
        align-self: center;
        justify-self: center;
    }
`

export const PersonalLinks = styled.div`
    display: flex;
    flex-direction: row;
    width: 130px;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 40px;

`

export const Link = styled.a`
    margin-bottom: 28px;
    
`

export const Logo = styled.img`
    height: 30px;
    width: 30px;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.1);
    }
`

export const FiverrLogo = styled(SiFiverr)`
    src: SiFiverr;
    height: 30px;
    width: 30px;
    fill: #fff;
    transition: all 0.2s ease-in-out;

    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.1);
    }
`

export const Text = styled.p`
    color: #fff;
    align-self: center;
    justify-self: center;
    letter-spacing: 2px;
    font-size: 12px;
    text-align: center;
    transition: all 0.2s ease-in-out;
    font-weight: 400;
    margin-top: 30px;
    @media screen and (max-width: 768px) {
        display: none;
    }
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.03);
    }
    @media screen and (min-width: 768px) {
        margin-top: 0px;
        margin-bottom: 30px;
    }
`

export const HeadingWrapper = styled.div`
    @media screen and (max-width: 768px) {
        margin-top: 80px;
    }
`

export const Text1 = styled.p`
    color: #fff;
    align-self: center;
    justify-self: center;
    letter-spacing: 2px;
    font-size: 12px;
    text-align: center;
    transition: all 0.2s ease-in-out;
    font-weight: 400;
    margin-top: 30px;
    &:hover {
        transition: all 0.2s ease-in-out;
        transform: scale(1.03);
    }
    @media screen and (min-width: 768px) {
        margin-top: 0px;
        margin-bottom: 30px;
        display: none;
    }
`

export const LinksWrapper = styled.div`
    display: flex;
    flex-direction: column;
    @media screen and (min-width: 768px) {
        flex-direction: row;
        align-items: center;
        justify-content: space-evenly;
    }
`